
<template>
    <router-link to="" tag="div">

        <div class ="submenu">

            <ul>
                <li class="title">Exam {{ examNumber }}</li>
                <li class="subButton" @click='toggle()'><a>{{toggleText}}</a></li>
            </ul>


        </div>

        <p class="headerDesc">Below are the examinable topics for exam {{ examNumber }}. Click each topic to view the related subtopics</p>
        <div class="topicContainer">

            <div class="box topicLayout" v-for="topic in topics" v-bind:key="topic" v-on:click='toggle()'>
                <p>
                    {{topic.topic}}
                </p>
                <ul>
                    <li v-for="st in topic.subtopics" v-bind:key="st" v-show="seen">
                        {{st}}
                    </li>
                </ul>
            </div>
        </div>
    </router-link>

</template>

<script>
    export default {
        name: 'topics',
        props: ["examNumber"],
        data () {
            return {
                topics: [],
                seen:false,
                toggleText: 'Show details'
            }},

        mounted() {
            fetch("http://localhost:8080/topics/"+this.examNumber)
                .then(response => response.json())
                .then((data) => {
                    console.log(data);
                    this.topics = data;

                })
        },
        methods:{
            toggle: function(){
                this.seen = !this.seen;
                this.toggleText=this.seen ? 'Hide details' : 'Show details';
                return this.toggleText
            }
        }
    }
</script>


